import { useState } from "react";
import axios from "axios";

type Props = {
  token: string;
  onClose: () => void;
  onSuccess: () => void;
};

export default function UploadModal({ token, onClose, onSuccess }: Props) {
  const [file, setFile] = useState<File | null>(null);
  const [title, setTitle] = useState("");
  const [accessType, setAccessType] = useState("Public");
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  const userEmail = typeof window !== "undefined" ? localStorage.getItem("userEmail") : "";

  const handleUpload = async () => {
    if (!file || !title || !accessType || !token || !userEmail) {
      setError("Missing required fields.");
      return;
    }

    const ext = file.name.split(".").pop()?.toLowerCase();
    const sizeMB = file.size / (1024 * 1024);
    if (!["pdf", "docx", "xlsx", "jpg", "jpeg", "png", "txt"].includes(ext || "")) {
      setError("Unsupported file type.");
      return;
    }
    if (sizeMB > 10) {
      setError("File exceeds 10 MB limit.");
      return;
    }

    const formData = new FormData();
    formData.append("file", file);
    formData.append("title", title);
    formData.append("accessType", accessType);

    try {
      const url = `${process.env.NEXT_PUBLIC_API_URL}/documents/upload`;
      console.log("üì§ Uploading file to:", url);
console.log("üîê Token:", token);
      const response = await axios.post(url, formData, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "multipart/form-data"
        }
      });

      console.log("‚úÖ Upload response:", response.data);
      setSuccess("File uploaded successfully.");
      setError("");
      onClose();
      onSuccess();
    } catch (err: any) {
      console.error("‚ùå Upload failed:", err.response?.status, err.response?.data || err.message);
      setError("Failed to upload file.");
      setSuccess("");
    }
  };

  return (
    <div style={{
      position: "fixed", top: 0, left: 0, width: "100%", height: "100%",
      backgroundColor: "rgba(0,0,0,0.5)", display: "flex", justifyContent: "center", alignItems: "center"
    }}>
      <div style={{ backgroundColor: "#fff", padding: "2rem", borderRadius: "8px", width: "400px" }}>
        <h3>Upload Document</h3>

        <input
          type="text"
          placeholder="Document title"
          value={title}
          onChange={e => setTitle(e.target.value)}
          style={{ width: "100%", marginBottom: "1rem", padding: "0.5rem" }}
        />

        <select
          value={accessType}
          onChange={e => setAccessType(e.target.value)}
          style={{ width: "100%", marginBottom: "1rem", padding: "0.5rem" }}
        >
          <option value="Public">Public</option>
          <option value="Private">Private</option>
          <option value="Restricted">Restricted</option>
        </select>

        <input
          type="file"
          onChange={e => setFile(e.target.files?.[0] || null)}
          style={{ marginBottom: "1rem" }}
        />

        <button onClick={handleUpload} disabled={!file || !title}>
          Upload
        </button>
        <button onClick={onClose} style={{ marginLeft: "1rem" }}>
          Cancel
        </button>

        {error && <p style={{ color: "red", marginTop: "1rem" }}>{error}</p>}
        {success && <p style={{ color: "green", marginTop: "1rem" }}>{success}</p>}
      </div>
    </div>
  );
}
